<template>
  <Navbar @onButton="buttonHumberger"></Navbar>
  <Header :isMobile="isMobile" :isOnButton="isOnButton"></Header>
  <Aboutme></Aboutme>
  <OurServices></OurServices>
  <Project></Project>
  <Footer :isMobile="isMobile"></Footer>
</template>

<script>
import Navbar from "./components/Navbar.vue";
import Header from "./components/Header.vue";
import Aboutme from "./components/Aboutme.vue";
import OurServices from "./components/OurServices.vue";
import Project from "./components/Project.vue";
import Footer from "./components/Footer.vue";

export default {
  name: "App",
  components: {
    Navbar,
    Header,
    Aboutme,
    OurServices,
    Project,
    Footer,
  },
  data() {
    return {
      isMobile: null,
      isOnButton: null,
    };
  },
  methods: {
    showMobile() {
      this.isMobile = window.innerWidth >= 492;
    },
    buttonHumberger(isBtnOn) {
      this.isOnButton = isBtnOn;
      console.log(this.isOnButton);
    },
  },
  created() {
    this.showMobile();
  },
  computed() {
    this.showMobile();
  },
};
</script>

